export const FORMAT_PAINTER_COPY = Symbol('FORMAT_PAINTER_COPY');

export const FORMAT_PAINTER_CLEAR_CLIPBOARD = Symbol('FORMAT_PAINTER_CLEAR_CLIPBOARD');

export function copyFormat(copyType: string, defaultSz?: number) {
  return {
    type: FORMAT_PAINTER_COPY,
    payload: { copyType, defaultSz },
  };
}

export function clearFormatClipboard() {
  return {
    type: FORMAT_PAINTER_CLEAR_CLIPBOARD,
  };
}
